<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li> 显示商城所有的订单，涵盖平台自营和第三方商家订单</li>
      <li>自动同步只能针对待付款订单操作，该操作自动同步对应的第三方支付机构的支付结果</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>订单编号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.orderNum"></el-input>
      </div>
      <div class="query-group">
        <label>店铺名称</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.storeName"></el-input>
      </div>
      <div class="query-group">
        <label>买家账户</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.memberName"></el-input>
      </div>
      <div class="query-group">
        <label>订单状态</label>
        <el-select  @change="query" v-model="queryParams.status">
         <el-option label="全部" :value="' '"></el-option>
         <el-option
            v-for="item in orderStatus"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>订单类型</label>
        <el-select  @change="query" v-model="queryParams.orderType">
          <el-option label="全部"  value=""></el-option>
          <el-option label="实物订单" value="1"></el-option>
          <el-option label="虚拟订单" value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>支付方式</label>
        <el-select  @change="query" v-model="queryParams.payMode">
          <el-option label="全部" value=""></el-option>
          <el-option label="银联" value="3"></el-option>
          <el-option label="支付宝" value="1"></el-option>
          <el-option label="微信" value="2"></el-option>
          <el-option label="余额" value="4"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>订单来源</label>
        <el-select @change="query" v-model="queryParams.source">
          <el-option label="全部" value=""></el-option>
          <el-option label="pc端" value="1"></el-option>
          <el-option label="手机端" value="2"></el-option>
          <el-option label="APP" value="3"></el-option>
          <el-option label="公众号 " value="4"></el-option>
          <el-option label="小程序" value="5"></el-option>
          <el-option label="平板" value="6"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>下单时间</label>
        <cms-picker @change="setTimer" value-format="yyyy-MM-dd HH:mm:ss"></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-tabs v-model="queryParams.status" @tab-click="changeOrderStatus">
      <el-tab-pane label="所有订单" :name="' '"></el-tab-pane>
      <el-tab-pane
        v-for="item in orderStatus"
        :key="item.value"
        :label="item.label"
        :name="item.value"
      ></el-tab-pane>
    </el-tabs>
    <!-- 操作区 -->
    <table class="order-table" v-loading="loading">
      <tr>
        <th>商品 </th>
        <th>单价</th>
        <th>数量</th>
        <th>收货人</th>
        <th>实收款</th>
        <th>订单状态</th>
        <th>操作选项</th>
      </tr>
      <tbody v-for="(item,index) in tableData" :key="index">
        <div style="width:100%;height:25px;background:#fff;"></div>
        <!-- 头部 -->
        <tr class="order-header">
          <td colspan="7" style="border:1px solid #EBEEF5;">
            <!-- <span :class="item.orderType==1? 'circle':'circle-Two'" v-text="item.orderType==1?'实':'虚'"></span> -->
            <span style="margin-left:15px;">订单编号：</span>
            <span style="margin-right: 20px;">{{item.orderCode}}</span>
            <span>下单时间:</span>
            <span style="margin-right: 20px;">{{$moment(item.orderTime)}}</span>
            <div class="stroe-name">
              <i class="iconfont icon-dianpu"></i>
              <span class="img-span" v-if="item.store">{{item.store.storeName}}</span>
            </div>
          </td>
        </tr>
        <tr v-for="(product,index2) in item.items" :key="product.id">
          <td style="border-bottom:1px solid #EBEEF5;border-left:1px solid #EBEEF5;padding:15px;">
            <div style="overflow:hidden;min-height:82px;" v-if="item.items&&item.items.length>0">
              <img class="product-img" :src="$cms.filterUrl(product.productInfo.coverImageUrl)" 
              v-if="product.productInfo" />
              <div class="product-title">
                <h3 class="title-h3" v-if="product.productInfo">{{product.productInfo.productName}}</h3>
                <p class="title-p" style="margin-top:10px;" v-if="product.productInfo">
                  {{showAttr(product.productInfo.skuAttr)}}
                </p>
              </div>
            </div>
          </td>
          <td class="order-prices">
            <span v-if="item.items&&item.items.length>0 ">￥{{$cms.float(product.productPrice)}} </span>
          </td>
          <td class="order-count">
            <span v-if="item.items&&item.items.length>0">
              x {{product.productCount}}
            </span>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-user">
            <el-popover trigger="hover" placement="top">
             <p v-if="item.member.nickname">{{ item.member.nickname }}</p>
             <p v-if="!item.member.nickname">{{ item.member.username }}</p>
             <p>{{ item.orderExt.customerAddress}}</p>
             <p>{{ item.member.phone}}</p>
             <div slot="reference" class="name-wrapper"> 
               <i class="iconfont  icon-yonghutouxiang"></i>
               <span class="admin-p" v-if="item.member.nickname">{{item.member.nickname}}</span>
               <span class="admin-p" v-if="!item.member.nickname">{{item.member.username}}</span>
             </div>
            </el-popover>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="tr-price">
            <p class="await" style="marign-top:10px;">￥{{$cms.float(item.orderPayAmount)}}</p>
            <p style="color:#999999;">(含运费：￥{{$cms.float(item.orderFreight)}})</p>
            <p>
              <!-- <i class="iconfont icon-weixinzhifu" style="color:#00C700;margin-right:5px"></i> -->
               {{item.payMode |payModeFilter}}
            </p>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-status">
            <div class="await">
              <span v-if="item.status==1">待付款</span>
              <span v-if="item.status==2">待发货</span>
              <span v-if="item.status==3">待收货</span>
              <span v-if="item.status==4">已完成</span>
              <span v-if="item.status==5">已取消</span>
              <span v-if="item.status==6">超时交易关闭</span>
            </div>
            <p style="color:#4196E4;margin: 10px 0 10px 0;cursor:pointer;" class="link" 
              @click="$routerLink('/orderList/view','view',{id:item.id})">
              订单详情
            </p>
          </td>
          <td :rowspan="item.items.length" v-if="index2==0" class="order-operation">
            <div v-if="item.status==1">
              <cms-button type='info' icon='warning' slot="reference" @click="cancel(item.id)">取消</cms-button>
            </div>
            <!-- <div style="margin-top:10px">
              <cms-button type='info' icon='error'>删除</cms-button>
            </div> -->
            <div style="margin-top:10px" v-if="item.status==1">
              <cms-button type='rest' icon='rest' @click="sync(item.id)">查询交易结果</cms-button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { orderPage, orderCancel, orderSync } from '@/api/order/index.js'
var defaultData = {
  page: 1,
  size: 10,
  source: '',
  orderType: '',
  status: ' ',
  payMode: '',
  orderNum: '',
  storeName: '',
  orderTimeBegin: '',
  orderTimeEnd: '',
  memberName: ''
}
export default {
  name: 'orderList',
  mixins: [list],
  data() {
    return {
      orderStatus: [
        {
          label: '待付款 ',
          value: '1'
        },
        {
          label: '待发货',
          value: '2'
        },
        {
          label: '待收货',
          value: '3'
        },
        {
          label: '已完成 ',
          value: '4'
        },
        {
          label: '已取消',
          value: '5'
        }
      ],
      queryParams: Object.assign({}, defaultData)
    }
  },
  filters: {
    payModeFilter(value) {
      if (value === '') {
        return ''
      } else {
        const payMode = ['', '支付宝', '微信', '银联', '余额']
        return payMode[value]
      }
    }
  },
  methods: {
    cancel(id) { // 取消
      this.$confirm('确定取消该订单?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        orderCancel({ orderId: id }).then(res => {
          if (res.code === 200) {
            this.$successMessage('取消成功')
            this.getTableData(orderPage, this.queryParams)
          } else {
            this.$errorMessage(res.message)
          }
        })
      })
    },
    sync(id) { // 自动同步
      this.$confirm('是否查询交易结果?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        orderSync({ orderId: id }).then(res => {
          if (res.code === 200) {
            this.$successMessage('同步成功')
            this.getTableData(orderPage, this.queryParams)
          } else {
            this.$errorMessage(res.message)
          }
        })
      })
    },
    changeOrderStatus(value) {
      this.query()
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.orderTimeBegin = timer[0]
      this.queryParams.orderTimeEnd = timer[1]
      this.query()
    },
    showAttr(data) {
      if (data === '') {
        return ''
      } else {
        const arr = JSON.parse(data)
        let str = ''
        arr.forEach(v => {
          const tmp = v.name + ':' + v.value + '  '
          str = str + tmp
        })
        return str
      }
    }
  },
  created() {
    this.getTableData(orderPage, this.queryParams)
  }
}
</script>

<style scoped lang='scss'>
.order-table {
  tbody {
    width: 100%;
  }
}
.order-header {
  background: #fcfcfc;
  height: 38px;
  td {
    height: 38px;
  }
  .stroe-name {
    display: inline-block;
  }
}

.circle {
  width: 25px;
  display: inline-block;
  height: 25px;
  text-align: center;
  margin-top: 3px;
  line-height: 25px;
  border-radius: 25px;
  color: #fdbe63;
  border: 1px solid #fdbe63;
  margin-right: 10px;
  margin-left: 10px;
}
.circle-Two {
  width: 30px;
  display: inline-block;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-radius: 30px;
  color: #71c1ff;
  margin-top: 5px;
  border: 1px solid #71c1ff;
  margin-right: 20px;
}
.img-span {
  font-size: 12px;
  color: #777777;
}

.icon-dianpu {
  color: #00cbbc;
}

.product-img {
  width: 60px;
  height: 60px;
  float: left;
}
.product-title {
  margin-left: 74px;
  max-width: 296px;
}
.order-prices {
  width: 10%;
  text-align: center;
  border-bottom:1px solid #EBEEF5;
  vertical-align: top;
  padding-top: 20px;
}
.order-count {
  width: 9%;
  text-align: center;
  padding-top: 20px;
}
.order-user {
  padding: 5px 20px 0 15px;
  width: 12%;
}
.tr-price {
  min-width: 170px;
  padding: 5px;
}
.order-status {
  text-align: center;
  min-width: 160px;
  padding-top: 20px;
}
.order-operation {
  min-width: 140px;
  padding: 20px;
}
.span-title {
  font-size: 14px;
  color: #000000;
}
.icon-yonghutouxiang {
  font-size: 30px;
  position: relative;
  top: 5px;
  left: 7px;
}

.admin-p {
  margin-top: 11px;
}
.mobalOrder {
  width: 66px;
  height: 20px;
  background-image: linear-gradient(0deg, #5fb5ec 0%, #b3dcfb 100%),
    linear-gradient(#ffb675, #ffb675);
  background-blend-mode: normal, normal;
  border: solid 0px #9bd4ff;
  font-size: 10px;
  color: #ffffff;
  text-align: center;
}
.title-h3 {
  margin-top: 0
}
.title-p {
  font-size: 12px;
  color: #999999;
}
.await {
  font-size: 14px;
  color: #353535;
}
</style>




